import React, { Component } from 'react';

export default class Footer extends Component {

    render() {
        return (
            <section id="footer">
            <ul className="icons">
                <li><a href="/" className="icon brands alt fa-twitter"><span className="label">Twitter</span></a></li>
                <li><a href="/" className="icon brands alt fa-facebook-f"><span className="label">Facebook</span></a></li>
                <li><a href="/" className="icon brands alt fa-instagram"><span className="label">Instagram</span></a></li>
                <li><a href="/" className="icon brands alt fa-github"><span className="label">GitHub</span></a></li>
                <li><a href="/" className="icon solid alt fa-envelope"><span className="label">Email</span></a></li>
            </ul>
            <ul className="copyright">
                <li>&copy; Untitled</li><li>Design: <a href="http://html5up.net">HTML5 UP</a></li><li>Demo Images: <a href="http://unsplash.com">Unsplash</a></li>
            </ul>
        </section>
        );
    }
}